#back-to-top {
  width: 10rem;
  right: 0;
  left: auto;
  padding-left: 0 !important;
}

.footer-logos {
  max-width: 400px;
}

h2,
h3 {
  &.sticky-top {
    top: 5.2rem;
    z-index: 100;

    @media (max-width: 600px) {
      top: 8rem;
    }
  }
}

.btn-toolbar {
  z-index: 200;
}

.btn-group .btn {
  line-height: 2;
}

.mt-n5{
  margin-top: -5rem;
}